import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, OrderCreateData, OrderCreateResp, IOrderCreateApi } from './types';

export default class OrderCreateStore implements IOrderCreateApi {
	private readonly _apiStore = RootStore.apiStore;

	async orderCreate(data: OrderCreateData): Promise<ApiResp<OrderCreateResp>> {
		const response = await this._apiStore.request<ApiResponse<OrderCreateResp, OrderCreateResp>>({
			method: HTTPMethod.POST,
			endpoint: `/api/v1/order/create`,
			headers: {
				accept: 'application/json',
				'Content-Type': 'application/json',
			},
			data: data,
		});
		return {
			success: response.success,
			data: response.data,
			status: response.status,
		};
	}
}
