import React from 'react';
import Input from '../Input';
import dropdownArrow from 'public/static/img/dropdownArrow.svg';

import styles from './Dropdown.module.scss';
import Image from 'next/image';
import { SuisseIntl } from 'helpers/utils/fonts';
import InfiniteScroll from 'react-infinite-scroll-component';

type Dropdowntypes = {
	placeholder?: string;
	value: string;
	id?: string;
	disable?: boolean;
	isError?: boolean;
	errorText?: string;
	className?: string;
	readonly?: boolean;
	isOnlySelect?: boolean;
	dropdata?:
		| {
				id: number;
				name: string;
		  }[]
		| string[];
	withSuisse?: boolean;
	infinityScroll?: boolean;
	hasMore?: boolean;
	inputType?: string;
	onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
	fetchData?: () => void;
	clickOnDropData: (event: React.MouseEvent<HTMLDivElement>) => void;
};

const Dropdown = ({
	placeholder,
	value,
	id,
	disable,
	isError,
	errorText,
	className,
	readonly,
	isOnlySelect,
	dropdata,
	withSuisse,
	infinityScroll,
	hasMore,
	inputType,
	onChange,
	fetchData,
	clickOnDropData,
}: Dropdowntypes) => {
	const [dropdownIsOpen, setDropdownIsOpen] = React.useState<boolean>(isOnlySelect ? false : true);

	const dropdownRef = React.useRef<HTMLDivElement | null>(null);
	const dropdownInputRef = React.useRef<HTMLDivElement | null>(null);

	const onChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
		onChange(event);
		setDropdownIsOpen(true);
	};

	const openDropdown = () => {
		setDropdownIsOpen(!dropdownIsOpen);
	};

	const clickOnDropDataHandler = (event: React.MouseEvent<HTMLDivElement>) => {
		clickOnDropData(event);
		setDropdownIsOpen(false);
	};

	const closeDropdownWithClick = (event: Event) => {
		const { target } = event;
		if (!dropdownRef.current?.contains(target as Node) && !dropdownInputRef.current?.contains(target as Node)) {
			setDropdownIsOpen(false);
		}
	};

	React.useEffect(() => {
		document.addEventListener('click', closeDropdownWithClick, { capture: true });
		return () => document.removeEventListener('click', closeDropdownWithClick);
	}, []);

	return (
		<div className={styles.dropdown}>
			{isOnlySelect ? (
				<div ref={dropdownInputRef} className={styles.selectedFieldContainer} onClick={openDropdown}>
					<div className={styles.selectedField}>{value}</div>
					<Image
						style={dropdownIsOpen ? { transform: 'rotate(180deg) translateY(50%)' } : {}}
						src={dropdownArrow}
						width={16}
						height={16}
						alt="v"
					/>
				</div>
			) : (
				<Input
					refInput={dropdownInputRef}
					type={inputType}
					placeholder={placeholder}
					value={value}
					id={id}
					isError={isError}
					errorText={errorText}
					className={className}
					readonly={disable || (value.length === 0 ? false : readonly)}
					onChange={onChangeInput}
				/>
			)}
			{Array.isArray(dropdata) && dropdata.length > 0 && dropdownIsOpen && !readonly && (
				<div
					ref={dropdownRef}
					style={{ overflowY: dropdata.length > 3 ? 'scroll' : 'auto' }}
					className={`${styles.dropdata} ${withSuisse ? SuisseIntl.className : ''}`}
				>
					{!infinityScroll &&
						dropdata.map(item => {
							return (
								<div
									style={item.name === value ? { backgroundColor: 'rgba(35, 35, 35, 0.2)' } : {}}
									id={item.id ? item.id.toString() : item}
									onClick={disable ? null : clickOnDropDataHandler}
									key={item.id ? item.id : item}
									className={styles.dropdataItem}
								>
									{item.name ? item.name : item}
								</div>
							);
						})}
					{infinityScroll && (
						<InfiniteScroll
							dataLength={dropdata.length}
							next={fetchData}
							height={dropdata.length >= 3 ? 96 : dropdata.length * 32}
							hasMore={hasMore}
							loader={''}
						>
							{dropdata.map(item => {
								return (
									<div
										style={item.name === value ? { backgroundColor: 'rgba(35, 35, 35, 0.2)' } : {}}
										id={item.id.toString()}
										onClick={disable ? null : clickOnDropDataHandler}
										key={item.id}
										className={styles.dropdataItem}
									>
										{item.name}
									</div>
								);
							})}
						</InfiniteScroll>
					)}
				</div>
			)}
		</div>
	);
};

export default Dropdown;
