import Col from 'components/UI/Col';
import Dropdown from 'components/UI/Dropdown';
import Input from 'components/UI/Input';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import useDebounce from 'helpers/hooks/useDebounce';
import React from 'react';
import { useSelector } from 'react-redux';
import { changeCurrentStreet, changeStreets } from 'store/checkout/LocationDataStore';
import { getStreetList } from 'store/checkout/LocationDataStore/middleware';
import { changeAddress, changeStreetError } from 'store/checkout/PersonalDataStore';

import styles from './AddressForm.module.scss';

type fieldsTypes = {
	value: string;
	placeholder: string;
	type: string;
	code: string;
	isError: boolean;
	errorText: string;
	id: string;
}[];

const AddressForm = () => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const home = useSelector((state: any) => state.personalData.home);
	const currentStreet = useSelector((state: any) => state.locationData.currentStreet);
	const streetErr = useSelector((state: any) => state.personalData.street);
	const apartment = useSelector((state: any) => state.personalData.apartment);
	const comment = useSelector((state: any) => state.personalData.comment);
	const streets = useSelector((state: any) => state.locationData.streets);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const checkout = useSelector((state: any) => state.checkout.checkout);

	const dispatch = useAppDispatch();

	const fields = [
		{
			value: checkout.fields.find(item => item.code === 'HOME')?.value || '',
			placeholder: '',
			type: 'text',
			code: home.code,
			isError: home.isError,
			errorText: home.errorText,
			id: 'home',
		},
		{
			value: checkout.fields.find(item => item.code === 'FLAT')?.value || '',
			placeholder: '',
			type: 'text',
			code: apartment.code,
			isError: apartment.isError,
			errorText: apartment.errorText,
			id: 'apartment',
		},
		{
			value: '',
			placeholder: '  ',
			type: 'text',
			code: comment.code,
			isError: comment.isError,
			errorText: comment.errorText,
			id: 'comment',
		},
	];

	React.useEffect(() => {
		if (fieldList.length === 0) return;
		const fields = fieldList.map(item => {
			return {
				...item,
				isError: item.id === 'home' ? home.isError : item.id === 'apartment' ? apartment.isError : comment.isError,
				errorText:
					item.id === 'home' ? home.errorText : item.id === 'apartment' ? apartment.errorText : comment.errorText,
			};
		});
		setFieldList([...fields]);
	}, [home, apartment, comment]);

	const [fieldList, setFieldList] = React.useState<fieldsTypes>(fields);
	const [street, setStreet] = React.useState<string>(checkout.fields.find(item => item.code === 'STREET')?.value || '');
	const [streetIsReadonly, setstreetIsReadonly] = React.useState<boolean>(true);

	const debounceStreet = useDebounce(street, 300);

	const onChangeInputs = (event: React.ChangeEvent<HTMLInputElement>, id: string) => {
		setFieldList(prev => {
			return prev.map(item => {
				if (item.id === id) {
					return { ...item, value: event.target.value };
				}
				return item;
			});
		});
		dispatch(
			changeAddress({
				home: id === 'home' ? event.target.value : home.value,
				apartment: id === 'apartment' ? event.target.value : apartment.value,
				comment: id === 'comment' ? event.target.value : comment.value,
			})
		);
	};

	const changeStreet = (event: React.ChangeEvent<HTMLInputElement>) => {
		setStreet(event.target.value);
		dispatch(changeCurrentStreet(event.target.value));
		setstreetIsReadonly(false);
		dispatch(changeStreetError());
	};

	const clickOnDropDataStreet = (event: React.MouseEvent<HTMLDivElement>) => {
		setStreet(event.currentTarget.textContent);
		dispatch(changeCurrentStreet(event.currentTarget.textContent));
		dispatch(changeStreets([]));
		setstreetIsReadonly(true);
	};

	React.useEffect(() => {
		dispatch(
			getStreetList({
				city: currentCity,
				query: debounceStreet,
			})
		);
	}, [debounceStreet]);

	React.useEffect(() => {
		dispatch(changeCurrentStreet(currentStreet || street));
		setStreet(currentStreet || street);
		setFieldList(prev => {
			return prev.map(item => {
				if (item.code === 'HOME') {
					return { ...item, value: home.value || checkout.fields.find(item => item.code === 'HOME')?.value };
				} else if (item.code === 'FLAT') {
					return { ...item, value: apartment.value || checkout.fields.find(item => item.code === 'FLAT')?.value };
				}
				return item;
			});
		});
	}, []);

	return (
		<Col marginBottom={isMobile ? 40 : 60} marginTop={32}>
			<div className={styles.addressForm}>
				<Dropdown
					placeholder=""
					value={street}
					id={streetErr.code}
					readonly={streetIsReadonly}
					dropdata={streets}
					isError={streetErr.isError}
					errorText={streetErr.errorText}
					onChange={changeStreet}
					clickOnDropData={clickOnDropDataStreet}
				/>
				{fieldList.map(item => {
					return (
						<div key={item.id}>
							<Input
								placeholder={item.placeholder}
								value={item.value}
								isError={item.isError}
								errorText={item.errorText}
								type={item.type}
								id={item.code}
								onChange={event => onChangeInputs(event, item.id)}
							/>
						</div>
					);
				})}
			</div>
		</Col>
	);
};

export default React.memo(AddressForm);
