import { SuisseIntl } from 'helpers/utils/fonts';
import React from 'react';

import styles from './Radio.module.scss';

type RadioTypes = {
	id: number;
	value: boolean;
	label: string;
	disable?: boolean;
	withoutSuisseIntl?: boolean;
	onClick: () => void;
};

const Radio = ({ id, value, label, disable, withoutSuisseIntl, onClick }: RadioTypes) => {
	return (
		<div className={`${styles.radio} ${disable ? styles.disable : ''}`}>
			<input type="radio" checked={value} id={id.toString() + label} onChange={!disable ? onClick : null} />
			<label
				htmlFor={id.toString() + label}
				className={withoutSuisseIntl ? '' : SuisseIntl.className}
				style={withoutSuisseIntl ? { textTransform: 'none', lineHeight: '125%' } : {}}
			>
				{label}
			</label>
		</div>
	);
};

export default Radio;
