import { orderArrModel } from 'api/checkout/Models/checkoutModel';
import { OrderCreateResp } from 'api/checkout/OrderCreateApi/types';
import OrderCreateStore from 'api/checkout/OrderCreateApi';
import { ApiResponse } from 'api/RootApi/ApiStore/types';
import Button from 'components/UI/Button';
import SubTitle from 'components/UI/SubTitle';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { formattedDiscount, formattedPrice } from 'helpers/utils';
import Image from 'next/image';
import close from 'public/static/img/closeDark.svg';
import React from 'react';
import { useSelector } from 'react-redux';
import { getCheckoutData } from 'store/checkout/CheckoutStore';
import { setSdekError } from 'store/checkout/CheckoutStore/CheckoutStore';
import RootStore from 'store/RootStore';

import styles from './CheckoutInfo.module.scss';
import { changePersonalDataError } from 'store/checkout/PersonalDataStore';
import { useRouter } from 'next/router';
import routes from 'helpers/routes';
import Link from 'next/link';
import {
	changeBirthday,
	changeCity,
	changeEmail,
	changeFirstName,
	changeId,
	changeLastName,
	changeMiddleName,
	changePhone,
	changeToken,
	getUserData,
} from 'store/user/UserDataStore';
import { DataUserModel } from 'api/auth/models/DataUserModel';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import ym from 'react-yandex-metrika';

const orderCreateStore = new OrderCreateStore();

type CheckoutInfoTypes = {
	checkoutData: orderArrModel;
	activeTab: number;
	disable: boolean;
	setPaymentBtn?: (val: string) => void;
};

const CheckoutInfo = ({ checkoutData, activeTab, disable, setPaymentBtn }: CheckoutInfoTypes) => {
	const [promo, setPromo] = React.useState<string>('');
	const [isPromoAdded, setIsPromoAdded] = React.useState<boolean | null>(null);
	const [orderIsLoaded, setOrderIsLoaded] = React.useState<boolean>(true);

	const promoInputRef = React.useRef<HTMLInputElement | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const currentCityCode = useSelector((state: any) => state.locationData.currentCityCode);
	const currentStreet = useSelector((state: any) => state.locationData.currentStreet);
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);
	const deliveryId = useSelector((state: any) => state.checkout.deliveryId);
	const paymentId = useSelector((state: any) => state.checkout.paymentId);
	const checkout = useSelector((state: any) => state.checkout.checkout);
	const checkoutIsLoaded = useSelector((state: any) => state.checkout.checkoutIsLoaded);
	const promoData = useSelector((state: any) => state.checkout.promo);
	const checkoutPickupRadio = useSelector((state: any) => state.checkout.checkout.checkoutPickupRadio);
	const deliveryAddressValue = useSelector((state: any) => state.personalData.deliveryAddress.value);

	const dispatch = useAppDispatch();

	const router = useRouter();

	const requiredFields = React.useMemo(() => {
		return activeTab === 0
			? ['EMAIL', 'PHONE', 'NAME', 'SURNAME', 'HOME', 'FLAT', 'STREET']
			: ['EMAIL', 'PHONE', 'NAME', 'SURNAME'];
	}, [activeTab]);

	React.useEffect(() => {
		if (promoData === '') {
			setPromo('');
			setIsPromoAdded(null);
		}
	}, [promoData]);

	const onChangePromo = (event: React.ChangeEvent<HTMLInputElement>) => {
		setPromo(event.target.value);
		setIsPromoAdded(null);
	};

	const addPromoHandler = (isDelete: boolean) => {
		if (isDelete) {
			dispatch(
				getCheckoutData({
					country: currentCountry,
					coupon: '',
					location: currentCity,
					personTypeId: 1,
					deliveryId,
					paymentId,
				})
			);
			setPromo('');
			setIsPromoAdded(null);
			return;
		}
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promoInputRef.current.value,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
			})
		).then(res => {
			const response = res.payload as ApiResponse<orderArrModel, orderArrModel>;
			if (response.data.promoIsSuccess) {
				setIsPromoAdded(true);
				process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'promo-code');
			} else {
				setIsPromoAdded(false);
			}
		});
	};

	const orderCreate = () => {
		const errorData = Object.values(RootStore.getState().personalData)
			.map((item, index) => {
				const indexError = requiredFields.indexOf(item.code);
				if (item.code === 'STREET') {
					if (indexError !== -1 && (currentStreet === '' || currentStreet === null)) {
						document.getElementById('STREET').scrollIntoView({
							behavior: 'smooth',
							block: 'center',
						});
						return {
							[Object.keys(RootStore.getState().personalData)[index]]: ' ',
						};
					}
				}
				if (item.code !== 'STREET') {
					if (indexError !== -1 && (item.value === '' || item.value === null)) {
						document.getElementById(item.code).scrollIntoView({
							behavior: 'smooth',
							block: 'center',
						});
						return {
							[Object.keys(RootStore.getState().personalData)[index]]: ' ',
						};
					}
				}
			})
			.filter(Boolean)
			.reduce((resObj, item) => {
				return { ...resObj, ...item };
			}, {});
		if (Object.keys(errorData).length !== 0) {
			dispatch(changePersonalDataError(errorData));
			return;
		}
		const data = {
			PERSON_TYPE_ID: 1,
			PAY_SYSTEM_ID: paymentId,
			DELIVERY_ID: deliveryId,
			COUPON: promoData,
			ORDER_PROPERTIES: checkout.fields
				.map(item => {
					if (item.code === 'STREET') {
						return {
							['ORDER_PROP_' + item.id]: currentStreet,
						};
					}
					if (item.code === 'LOCATION') {
						return {
							['ORDER_PROP_' + item.id]: currentCityCode,
						};
					}
					if (item.code === 'CUSOM_LOCATION' && Number.isNaN(parseInt(currentCityCode))) {
						return {
							['ORDER_PROP_' + item.id]: currentCity,
						};
					}
					const currentPersonalData = Object.values(RootStore.getState().personalData).find(
						findItem => findItem.code === item.code
					);
					return {
						['ORDER_PROP_' + item.id]: currentPersonalData?.value,
					};
				})
				.filter(item => Object.values(item)[0])
				.reduce((resObj, item) => {
					return { ...resObj, ...item };
				}, {}),
		};

		if (checkoutPickupRadio.find(method => method.value)?.code === 'SDEK_PVZ' && !deliveryAddressValue) {
			dispatch(setSdekError());
			return;
		}

		setOrderIsLoaded(false);
		orderCreateStore.orderCreate({ data }).then(res => {
			const response = res as ApiResponse<OrderCreateResp, OrderCreateResp>;

			try {
				const msg = response.data.message?.ORDER_PROPS?.ORDER_PROP_3;
				if (msg[0].includes('   ,   ')) {
					router.push('/checkout?personal-area=sign-in&isOrder');
				}
			} catch (err) {}

			if (response.data.STATUS === 'Success') {
				process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'order-processed');
				process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' &&
					(window as any).dataLayer.push({
						ecommerce: {
							currencyCode: 'RUB',
							purchase: {
								actionField: {
									id: response.data.order_id,
									coupon: isPromoAdded === true ? promo : undefined,
								},
								products: checkout.products.map(item => {
									return {
										id: item.id,
										name: item.name,
										category: item.category,
										price: parseInt(item.price.replace(' ', '')),
										quantity: item.quantity,
										variant: item.color?.value,
										coupon: isPromoAdded === true ? promo : undefined,
									};
								}),
							},
						},
					});
			}
			if (response.data.message && Object.keys(response.data.message.ORDER_PROPS).length !== 0) {
				setOrderIsLoaded(true);
				const errorIds = Object.keys(response.data.message.ORDER_PROPS).map(item => item.match(/ORDER_PROP_(\d)/)[1]);
				const errorList = Object.values(response.data.message.ORDER_PROPS).map(item => item[0]);
				const errorCodes = checkout.fields.filter(item => errorIds.includes(item.id)).map(item => item.code);
				const errorData = Object.values(RootStore.getState().personalData)
					.map((item, index) => {
						const indexError = errorCodes.indexOf(item.code);
						if (indexError !== -1) {
							document.getElementById(item.code).scrollIntoView({
								behavior: 'smooth',
								block: 'center',
							});
							return {
								[Object.keys(RootStore.getState().personalData)[index]]: errorList[indexError],
							};
						}
					})
					.filter(Boolean)
					.reduce((resObj, item) => {
						return { ...resObj, ...item };
					}, {});

				dispatch(changePersonalDataError(errorData));
			} else if (response.data.token !== null) {
				localStorage.setItem('token', response.data.token.token);
				dispatch(getUserData({ token: response.data.token.token })).then(res => {
					const { data } = res.payload as ApiResponse<DataUserModel, DataUserModel>;
					if (data.message) {
						localStorage.removeItem('token');
					} else {
						dispatch(changeToken(response.data.token.token));
						dispatch(changeId(data.id));
						dispatch(changeFirstName(data.firstName));
						dispatch(changeEmail(data.email));
						dispatch(changePhone(data.phone));
						dispatch(changeBirthday(data.birthday));
						dispatch(changeCity(data.city));
						dispatch(changeLastName(data.lastName));
						dispatch(changeMiddleName(data.middleName));
					}
				});
				dispatch(getCheckoutProduct({ token: response.data.token.token }));
			}
			if (response.data.redirect_pay_url) {
				document.location.href = response.data.redirect_pay_url;
				dispatch(getCheckoutProduct({ token: response.data.token?.token || null }));
			} else if (response.data.order_id) {
				router.push(routes.checkout + `?order=${response.data.order_id}`);
				setPaymentBtn(response.data.payment_template);
				dispatch(getCheckoutProduct({ token: response.data.token?.token || null }));
			}
		});
	};

	return (
		<div className={styles.checkoutInfo}>
			<SubTitle marginTop={0} marginBottom={24} text=" " size={isMobile ? 22 : 28} withSuisse />
			<div className={styles.info}>
				<div>
					<p></p>
					<p>{checkoutData.productCount} .</p>
				</div>
				<div>
					<p> </p>
					<p>{formattedPrice(checkoutData.price)}</p>
				</div>
				{!!(isPromoAdded || checkoutData.discount) && (
					<div>
						<p></p>
						<p>{formattedDiscount(checkoutData.discount)}</p>
					</div>
				)}
				{/*<div>
					<p>  {CheckoutMockData.personalDiscountWithPercent}%</p>
					<p>{formattedDiscount(CheckoutMockData.personalDiscount)}</p>
				</div>*/}
				<div>
					<p></p>
					<p>{formattedPrice(checkoutData.deliveryPrice)}</p>
				</div>
				<div>
					<p></p>
					<p>{formattedPrice(checkoutData.totalPrice)}</p>
				</div>
			</div>
			<div className={styles.promo}>
				<input
					ref={promoInputRef}
					className={styles.promoInput}
					style={isPromoAdded === false ? { border: '1px solid #FF3A3A' } : {}}
					type="text"
					value={isPromoAdded === true ? '' : promo}
					readOnly={isPromoAdded === true}
					placeholder={isPromoAdded === true ? promo : ''}
					onChange={onChangePromo}
				/>
				{isPromoAdded === false && <p className={styles.errorText}>  </p>}
				{promo && isPromoAdded === null && (
					<button className={styles.promoButton} onClick={() => addPromoHandler(false)}>
						
					</button>
				)}
				{isPromoAdded === true && promo && (
					<button className={styles.promoButton} onClick={() => addPromoHandler(true)}>
						<Image src={close} width={20} height={20} alt="X" />
					</button>
				)}
			</div>
			<Button
				className={styles.sendBtn}
				text=" "
				isInvisible
				isActive
				withoutLoader={disable}
				disabled={!orderIsLoaded || !checkoutIsLoaded || disable}
				onClick={orderCreate}
			/>
			<div className={styles.policy}>
				<p>
					 &nbsp; &laquo; &raquo;, &nbsp; &nbsp;{' '}
					<Link href={routes.policy}> </Link> &nbsp;
					<Link href={routes.agreement}> </Link>
				</p>
			</div>
		</div>
	);
};

export default CheckoutInfo;
