import OrderInfoApi from 'api/order/OrderInfoApi';
import Col from 'components/UI/Col';
import Content from 'components/UI/Content';
import Loader from 'components/UI/Loader';
import SubTitle from 'components/UI/SubTitle';
import Title from 'components/UI/Title';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import routes from 'helpers/routes';
import Link from 'next/link';
import { useRouter } from 'next/router';
import React from 'react';
import { useSelector } from 'react-redux';
import ym from 'react-yandex-metrika';
import { changeCheckout, getCheckoutData } from 'store/checkout/CheckoutStore';

import styles from './Checkout.module.scss';
import AddressForm from './views/AddressForm';
import CheckoutInfo from './views/CheckoutInfo';
import DeliveryTabs from './views/DeliveryTabs';
import LocationBlock from './views/LocationBlock';
import OrderInfo from './views/OrderInfo';
import PaymentRadioGroup from './views/PaymentRadioGroup';
import PersonalDataForm from './views/PersonalDataForm';
import ProductList from './views/ProductList';
import { changeAddress } from 'store/checkout/PersonalDataStore';
import { orderArrModel } from 'api/checkout/Models/checkoutModel';
import { ApiResp } from 'api/checkout/BasketOrderApi/types';

const orderInfoApi = new OrderInfoApi();

const Checkout = () => {
	const [activeTab, setActiveTab] = React.useState<number>(0);
	const [paymentBtn, setPaymentBtn] = React.useState<string>('');

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const token = useSelector((state: any) => state.userData.token);
	const checkoutData = useSelector((state: any) => state.checkout.checkout);
	const checkoutIsLoaded = useSelector((state: any) => state.checkout.checkoutIsLoaded);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const deliveryId = useSelector((state: any) => state.checkout.deliveryId);
	const paymentId = useSelector((state: any) => state.checkout.paymentId);
	const promo = useSelector((state: any) => state.checkout.promo);
	const checkoutProduct = useSelector((state: any) => state.checkoutProduct.checkoutProduct);
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);

	const dispatch = useAppDispatch();

	const router = useRouter();

	const [orderId, setOrderId] = React.useState<string>('');
	const [orderIsPaid, setOrderIsPaid] = React.useState<boolean>(false);
	const [isOrderFound, setIsOrderFound] = React.useState<boolean>(false);

	const selectTabs = (tabIndex: number) => {
		setActiveTab(tabIndex);
	};

	React.useEffect(() => {
		if (router.query.order) {
			orderInfoApi.getOrderInfo({ id: parseInt(router.query.order as string) }).then(res => {
				if (res.data.orderId) {
					setOrderId(res.data.orderId?.toString() || '');
					setIsOrderFound(true);
				}
				if (res.data.paymentData) {
					setOrderIsPaid(res.data.orderStatusId === 'P');
				} else if (res.data.status === 'fail') {
					setIsOrderFound(false);
					setOrderId(router.query.order as string);
				}
			});
		}
	}, [router.query, token]);

	React.useEffect(() => {
		if (localStorage.getItem('token') && !token) return;
		dispatch(
			getCheckoutData({
				country: '',
				coupon: promo,
				location: '',
				personTypeId: 1,
				deliveryId,
				paymentId,
			})
		).then(res => {
			const response = res.payload as ApiResp<orderArrModel>;
			dispatch(
				changeAddress({
					home: response.data.fields?.find(item => item.code === 'HOME')?.value,
					apartment: response.data.fields?.find(item => item.code === 'FLAT')?.value,
					comment: '',
				})
			);
		});
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [token]);

	React.useEffect(() => {
		if (checkoutData && (!Array.isArray(checkoutData.checkoutPickupRadio) || checkoutData.checkoutPickupRadio.length === 0)) {
			setActiveTab(0);
		}
	}, [checkoutData]);

	React.useEffect(() => {
		if (localStorage.getItem('token') && !token) return;
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
			})
		);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [checkoutProduct]);

	React.useEffect(() => {
		if (sessionStorage.getItem('visitCheckout') !== 'true') {
			process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'visit-checkout');
			sessionStorage.setItem('visitCheckout', 'true');
		}
		return () => {
			dispatch(changeCheckout({ checkout: null }));
		};
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, []);

	if (orderId && isOrderFound) {
		return (
			<Content className={`${styles.content} ${styles.contentInfo}`}>
				<OrderInfo type="create" action={orderIsPaid ? '' : ''} orderId={orderId} paymentBtn={paymentBtn} />
			</Content>
		);
	}

	if (orderId && !isOrderFound) {
		return (
			<Content className={`${styles.content} ${styles.contentInfo}`}>
				<OrderInfo type="fail" orderId={orderId} />
			</Content>
		);
	}

	if (!checkoutData) {
		return (
			<Content className={`${styles.content} ${styles.contentInfo}`}>
				<Loader withProgress />
			</Content>
		);
	}

	if (checkoutData.message === 'Properties not found') {
		return (
			<Content className={`${styles.content} ${styles.contentInfo}`}>
				<OrderInfo type="empty" />
			</Content>
		);
	}

	return (
		<Content className={styles.content}>
			<>
				<Title text="" marginBottom={40} size={isMobile ? 32 : 46} withSuisse />
				<ProductList products={checkoutData.products} />
				<Col marginTop={isMobile ? -25 : 80} marginBottom={isMobile ? 100 : 0}>
					<>
						<Title text=" " size={isMobile ? 32 : 46} withSuisse marginBottom={isMobile ? 40 : 60} />
						<div className={styles.checkout}>
							<div className={styles.edits}>
								<div className={styles.personalData}>
									<SubTitle
										marginTop={0}
										marginBottom={0}
										text=""
										size={isMobile ? 22 : 28}
										withSuisse
									/>
									{!token && (
										<p>
											 ? <Link href={routes.checkout + routes.signIn}></Link>
										</p>
									)}
								</div>
								<PersonalDataForm />

								<SubTitle
									marginTop={0}
									marginBottom={isMobile ? 32 : 40}
									text=" "
									size={isMobile ? 22 : 28}
									withSuisse
								/>
								<LocationBlock />
								{currentCity && (
									<>
										<DeliveryTabs
											activeTab={activeTab}
											checkoutData={checkoutData}
											disable={!checkoutIsLoaded}
											selectTabs={selectTabs}
										/>

										{activeTab !== 1 && (
											<SubTitle
												marginTop={0}
												marginBottom={0}
												text=""
												size={isMobile ? 22 : 28}
												withSuisse
											/>
										)}
										{activeTab !== 1 && <AddressForm />}
										<SubTitle
											marginTop={0}
											marginBottom={0}
											text=""
											size={isMobile ? 22 : 28}
											withSuisse
										/>
										<PaymentRadioGroup disable={!checkoutIsLoaded} checkoutData={checkoutData} />
									</>
								)}
							</div>
							<div className={styles.info}>
								<CheckoutInfo
									activeTab={activeTab}
									checkoutData={checkoutData}
									disable={!currentCity || !paymentId || !deliveryId}
									setPaymentBtn={setPaymentBtn}
								/>
							</div>
						</div>
					</>
				</Col>
			</>
		</Content>
	);
};

export default React.memo(Checkout);
