type paymentDataApi = {
	pay_system_id: number;
	pay_system_name: string;
	payment_template: string;
	is_paid: 'N' | 'Y';
};

export type paymentDataModel = {
	paySystemId: number;
	paySystemName: string;
	paymentTemplate: string;
	isPaid: 'N' | 'Y';
};

export type OrderInfoApi = {
	order_id?: number;
	order_status_id?: 'N' | 'P' | 'F';
	payment_data?: paymentDataApi[];
	status?: string;
};

export type OrderInfoModel = {
	orderId?: number;
	orderStatusId?: 'N' | 'P' | 'F';
	paymentData?: paymentDataModel[];
	status?: string;
};

export const normalizeOrderInfoArr = (from: OrderInfoApi): OrderInfoModel => {
	if (from.status === 'fail') {
		return {
			status: from.status,
		};
	}
	return {
		orderId: from.order_id,
		orderStatusId: from.order_status_id,
		paymentData: from.payment_data.map(item => {
			return {
				paySystemId: item.pay_system_id,
				paySystemName: item.pay_system_name,
				paymentTemplate: item.payment_template,
				isPaid: item.is_paid,
			};
		}),
	};
};
