import React from 'react';
import { useTranslation } from 'next-i18next';

import styles from './SizeInput.module.scss';
import { SuisseIntl } from 'helpers/utils/fonts';

type SizeInputTypes = {
	sizeList: {
		id: number;
		name: string;
	}[];
	disable: boolean;
	currentSize: string;
	onClickSizeHandler: (event: React.MouseEvent<HTMLDivElement>) => void;
};

const SizeInput = ({ sizeList, disable, currentSize, onClickSizeHandler }: SizeInputTypes) => {
	const { t } = useTranslation();

	return (
		<div className={styles.sizeList}>
			<p>{t('size')}</p>
			<div className={styles.sizes}>
				{sizeList.map(sizeItem => {
					return (
						<div
							style={sizeItem.name === currentSize ? { borderColor: '#1B2530', color: '#1B2530' } : {}}
							className={`${styles.size} ${SuisseIntl.className}`}
							onClick={disable ? null : onClickSizeHandler}
							id={sizeItem.name}
							key={sizeItem.id}
						>
							{sizeItem.name}
						</div>
					);
				})}
			</div>
		</div>
	);
};

export default SizeInput;
