type RadioListTypes = {
	id: number;
	value: boolean;
	label: string;
	code: string;
	disable?: boolean;
	isPrompt?: boolean;
}[];

type PaymentRadioGroupTypes = {
	checkoutData: orderArrModel;
	disable: boolean;
};

const PaymentRadioGroup = ({ checkoutData, disable }: PaymentRadioGroupTypes) => {
	const [paymentList, setPaymentList] = React.useState<RadioListTypes>(
		checkoutData.checkoutPaymentRadio ? checkoutData.checkoutPaymentRadio : []
	);
	//const [promptValue, setPromptValue] = React.useState<string | null>(null);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);
	const deliveryId = useSelector((state: any) => state.checkout.deliveryId);
	const promo = useSelector((state: any) => state.checkout.promo);

	const dispatch = useAppDispatch();

	const selectPaymentType = (id: number) => {
		if (disable) return;
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId: id,
			})
		);
	};

	//const clickOnPrompt = (code: string, isOpen: boolean) => {
	//	setPromptValue(isOpen ? code : null);
	//};

	React.useEffect(() => {
		setPaymentList(checkoutData.checkoutPaymentRadio);
	}, [checkoutData.checkoutPaymentRadio]);

	React.useEffect(() => {
		const paymentCash = paymentList?.find(item => item.code === 'PAYMENT_CASH');
		if (paymentCash && paymentCash?.value && paymentCash?.disable) {
			dispatch(
				getCheckoutData({
					country: currentCountry,
					coupon: promo,
					location: currentCity,
					personTypeId: 1,
					deliveryId,
					paymentId: checkoutData?.checkoutPaymentRadio[0]?.id,
				})
			);
		}
	}, [paymentList, checkoutData]);

	return (
		<Col className={styles.radioGroup} marginTop={32} marginBottom={isMobile ? 0 : 100}>
			<>
				<RadioGroup
					radioList={paymentList}
					selectType={code => selectPaymentType(code)} /*clickOnPrompt={clickOnPrompt}*/
				/>
				{/*<DetailSidebarInfo isOpen={promptValue !== null} onCloseInfo={() => clickOnPrompt(null, false)}>
					{promptValue === 'DOLYAMI' ? (
						<>
							<SubTitle
								marginTop={0}
								marginBottom={isMobile ? 24 : 40}
								text=" "
								size={isMobile ? 22 : 28}
								withSuisse
							/>
							<p className={styles.sidebarText}>
								 &nbsp;   &laquo;&raquo;&nbsp;&mdash;  , 
								   &nbsp;   &nbsp; &nbsp;&nbsp;
								&nbsp; .      , &nbsp;
								&nbsp;&nbsp;  .      &nbsp;.
							</p>
							<p className={`${styles.sidebarTitle} ${SuisseIntl.className}`}></p>
							<p className={styles.sidebarText}>
								&laquo;&raquo; &nbsp;   &nbsp;&nbsp; 
								 &nbsp;. &nbsp;&laquo;&raquo;   &nbsp;&mdash; 
								   ,  &nbsp; .  &laquo;&raquo;
								  &nbsp; &nbsp;30&nbsp;000     .
							</p>
							<p className={`${styles.sidebarTitle} ${SuisseIntl.className}`}> </p>
							<p className={`${styles.sidebarText} ${styles.lastSidebarText}`}>
								 &laquo;&raquo;    &nbsp; &nbsp;  . 
								  ,  &nbsp;&mdash;    .   
								    &nbsp; .
							</p>
							<p className={`${styles.sidebarText} ${styles.lastSidebarText}`}>
								  ,    &nbsp;,  &nbsp;
								.  &nbsp;    &nbsp;  &laquo;&raquo;,
								   . &nbsp;      
								    .
							</p>
						</>
					) : (
						<>
							<SubTitle
								className={styles.installment}
								marginTop={0}
								marginBottom={isMobile ? 24 : 40}
								text=" "
								size={isMobile ? 22 : 28}
								withSuisse
							/>
							<p className={styles.sidebarText}>
								   &nbsp;,     &nbsp; 
								&nbsp;&laquo;&raquo;.  ,     
								&nbsp;.
							</p>
							<p className={`${styles.sidebarTitle} ${SuisseIntl.className}`}></p>
							<p className={styles.sidebarText}>
								 &nbsp;   &nbsp;   &nbsp;
								&nbsp; .      .   
								,   &nbsp;    . &nbsp;  ,
								    &nbsp;  .
							</p>
							<p className={`${styles.sidebarTitle} ${SuisseIntl.className}`}> </p>
							<p className={`${styles.sidebarText} ${styles.lastSidebarText}`}>
								 &nbsp;   &laquo;&raquo;,    
								&nbsp; .        &nbsp;
								 ,   :
							</p>
							<ul className={styles.sidebarUl}>
								<li> </li>
								<li> </li>
								<li> </li>
							</ul>
						</>
					)}
				</DetailSidebarInfo>*/}
			</>
		</Col>
	);
};

