import { orderArrModel } from 'api/checkout/Models/checkoutModel';
import Button from 'components/UI/Button';
import Col from 'components/UI/Col';
import RadioGroup from 'components/UI/RadioGroup';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import React from 'react';
import { useSelector } from 'react-redux';
import { getCheckoutData } from 'store/checkout/CheckoutStore';

import styles from './DeliveryTabs.module.scss';

type RadioListTypes = {
	id: number;
	value: boolean;
	label: string;
	disable?: boolean;
	description?: string;
	price?: number;
}[];

type DeliveryTabsTypes = {
	activeTab: number;
	disable: boolean;
	checkoutData: orderArrModel;
	selectTabs: (tabIndex: number) => void;
};

const DeliveryTabs = ({ activeTab, disable, checkoutData, selectTabs }: DeliveryTabsTypes) => {
	const [deliveryList, setDeliveryList] = React.useState<RadioListTypes>(checkoutData.checkoutDeliveryRadio);
	const [pickupList, setPickupList] = React.useState<RadioListTypes>(checkoutData.checkoutPickupRadio);

	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);
	const paymentId = useSelector((state: any) => state.checkout.paymentId);
	const deliveryAddress = useSelector((state: any) => state.personalData.deliveryAddress);
	const promo = useSelector((state: any) => state.checkout.promo);
	const checkoutPickupRadio = useSelector((state: any) => state.checkout.checkout.checkoutPickupRadio);
	const deliveryAddressValue = useSelector((state: any) => state.personalData.deliveryAddress.value);
	const sdekError = useSelector((state: any) => state.checkout.sdekError);

	const dispatch = useAppDispatch();

	const selectDeliveryType = (id: number) => {
		if (disable) return;
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId: id,
				paymentId,
			})
		);
	};

	React.useEffect(() => {
		setDeliveryList(checkoutData.checkoutDeliveryRadio);
		setPickupList(checkoutData.checkoutPickupRadio);
	}, [checkoutData.checkoutDeliveryRadio, checkoutData.checkoutPickupRadio]);

	return (
		<Col marginTop={isMobile ? 32 : 40} marginBottom={isMobile ? 40 : 60}>
			<>
				<p className={styles.title}> </p>
				<div className={styles.buttonGroup}>
					<Button
						onClick={() => selectTabs(0)}
						style={activeTab === 1 ? { border: '1px solid rgba(35, 35, 35, 0.2)' } : {}}
						text=""
						isInvisible
					/>
					{Array.isArray(pickupList) && pickupList.length !== 0 && (
						<Button
							onClick={() => selectTabs(1)}
							style={activeTab === 0 ? { border: '1px solid rgba(35, 35, 35, 0.2)' } : {}}
							text=""
							isInvisible
						/>
					)}
				</div>
				<div className={styles.radioGroup}>
					{activeTab === 0 ? (
						<RadioGroup
							radioList={deliveryList}
							deliveryAddress={deliveryAddress}
							selectType={id => selectDeliveryType(id)}
						/>
					) : (
						<RadioGroup
							radioList={pickupList}
							deliveryAddress={deliveryAddress}
							selectType={id => selectDeliveryType(id)}
							sdekError={
								checkoutPickupRadio.find(method => method.value)?.code === 'SDEK_PVZ' &&
								!deliveryAddressValue &&
								sdekError
							}
						/>
					)}
				</div>
			</>
		</Col>
	);
};

export default React.memo(DeliveryTabs);
