import { normalizeOrderInfoArr, OrderInfoModel } from 'api/order/models/orderInfo';
import RootStore from 'api/RootApi';
import { ApiResponse, HTTPMethod } from 'api/RootApi/ApiStore/types';

import { ApiResp, OrderInfoApiParams, IOrderInfoApi } from './types';

export default class OrderInfoApi implements IOrderInfoApi {
	private readonly _apiStore = RootStore.apiStore;

	async getOrderInfo(params: OrderInfoApiParams): Promise<ApiResp> {
		const response = await this._apiStore.request<ApiResponse<OrderInfoModel, OrderInfoModel>>({
			method: HTTPMethod.GET,
			endpoint: `/api/v1/order/${params.id}`,
		});
		try {
			response.data = normalizeOrderInfoArr(response.data);
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		} catch (e) {
			return {
				success: response.success,
				data: response.data,
				status: response.status,
			};
		}
	}
}
