import { formattedPrice } from 'helpers/utils';
import { SuisseIntl } from 'helpers/utils/fonts';
import Image from 'next/image';
import React from 'react';
import Radio from '../Radio/Radio';
import promptImg from 'public/static/img/prompt.svg';

import styles from './RadioGroup.module.scss';
import DOMPurify from 'isomorphic-dompurify';

type RadioGroupTypes = {
	radioList: {
		id: number;
		value: boolean;
		label: string;
		description?: string;
		price?: number;
		code?: string;
		disable?: boolean;
		isPrompt?: boolean;
	}[];
	deliveryAddress?: {
		code: string;
		isError: boolean;
		errorText: string;
		value: string;
	};
	withoutBorder?: boolean;
	sdekError?: boolean;
	selectType: (id: number) => void;
	clickOnPrompt?: (id: string, isOpen: boolean) => void;
};

const RadioGroup = ({ radioList, deliveryAddress, withoutBorder, sdekError, selectType, clickOnPrompt }: RadioGroupTypes) => {
	return (
		<div className={`${styles.radioGroup}`}>
			{radioList.map(item => {
				return (
					<div
						key={item.id}
						onClick={item.code === 'SDEK_PVZ' && !deliveryAddress.value ? () => globalThis.orderWidjet.open() : null}
						className={`${styles.radio} ${withoutBorder ? styles.withoutBorder : ''} ${
							deliveryAddress ? (item.code === 'SDEK_PVZ' && deliveryAddress.isError ? styles.error : '') : ''
						}`}
					>
						<div>
							<Radio
								id={item.id}
								value={item.value}
								label={item.label}
								disable={item.disable}
								withoutSuisseIntl={withoutBorder}
								onClick={() => selectType(item.id)}
							/>
							{typeof item.price === 'number' && item.value && (
								<p className={SuisseIntl.className}>{formattedPrice(item.price)}</p>
							)}
							{item.isPrompt && (
								<Image
									src={promptImg}
									width={16}
									height={16}
									alt="?"
									onClick={() => clickOnPrompt(item.code, true)}
								/>
							)}
						</div>
						{item.value && item.description && (
							<p dangerouslySetInnerHTML={{ __html: DOMPurify.sanitize(item.description) }}></p>
						)}
						{item.code === 'SDEK_PVZ' && item.value && deliveryAddress.value && (
							<p id="address-SDEK-delivery"> : {deliveryAddress.value}</p>
						)}
						{item.code === 'SDEK_PVZ' && item.value && (
							<p
								className={styles.widjetLink}
								onClick={item.code === 'SDEK_PVZ' ? () => globalThis.orderWidjet.open() : null}
							>
								{deliveryAddress.value ? '  ' : '  '}
							</p>
						)}
						{item.code === 'SDEK_PVZ' && sdekError && (
							<span className={styles.errorText}>,   </span>
						)}
					</div>
				);
			})}
		</div>
	);
};

export default RadioGroup;
