import React from 'react';
import { useTranslation } from 'next-i18next';

import styles from './CounterInput.module.scss';
import { SuisseIntl } from 'helpers/utils/fonts';

type CounterInputTypes = {
	count: number;
	disable: boolean;
	leftBtnClick: (event: React.MouseEvent) => void;
	rightBtnClick: (event: React.MouseEvent) => void;
};

const CounterInput = ({ count, disable, leftBtnClick, rightBtnClick }: CounterInputTypes) => {
	const { t } = useTranslation();

	return (
		<div className={styles.counter}>
			<p>{t('quantity')}</p>
			<div className={styles.count}>
				<button className={SuisseIntl.className} onClick={disable ? null : leftBtnClick}>
					-
				</button>
				<div className={SuisseIntl.className}>{count}</div>
				<button className={SuisseIntl.className} onClick={disable ? null : rightBtnClick}>
					+
				</button>
			</div>
		</div>
	);
};

export default CounterInput;
