import Checkout from 'components/pages/Checkout';
import React from 'react';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Footer from 'components/Footer';
import Header from 'components/Header';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';
import { useGetUserData } from 'helpers/hooks/useGetUserData';
import { baseSSR } from 'helpers/utils/baseSSR';
import Head from 'next/head';
import Script from 'next/script';
import RootStore from 'store/RootStore';
import { changeDeliveryAddress } from 'store/checkout/PersonalDataStore';
import { useSelector } from 'react-redux';

const CheckoutPage = ({ data }) => {
	const dispatch = useAppDispatch();

	const city = useSelector((state: any) => state.userData.city);

	useGetUserData();

	dispatch(changeIsMobile(data.isMobile));

	return (
		<>
			<Head>
				<meta
					name="keywords"
					content=" ,  ,  ,  ,  "
				></meta>
				<meta
					name="description"
					content="TOTTI I  ,  .     ,   . ,   ."
				></meta>
			</Head>
			<Script
				id="ISDEKscript"
				src="https://widget.cdek.ru/widget/widjet.js"
				onReady={() => {
					(globalThis as any).orderWidjet =
						(globalThis as any).ISDEKWidjet &&
						new (globalThis as any).ISDEKWidjet({
							popup: true,
							hidedelt: true,
							defaultCity: city || '',
							cityFrom: city || '',
							onReady: function () {
								(globalThis as any).ipjq('#linkForWidjet').css('display', 'inline');
							},
							onChoose: function (info: any) {
								RootStore.dispatch(changeDeliveryAddress(info.PVZ.Address));
								(globalThis as any).orderWidjet.close();
							},
						});
				}}
			/>
			<Header />
			<Checkout />
			<Footer withDarkBGC={true} />
		</>
	);
};

export const getServerSideProps = async ({ req, locale }) => {
	const { isMobile } = await baseSSR(req);

	return {
		props: {
			data: {
				isMobile,
			},
			...(await serverSideTranslations(locale, ['common', 'checkout'])),
		},
	};
};

export default CheckoutPage;
