import CounterInput from 'components/UI/CounterInput';
import Dropdown from 'components/UI/Dropdown';
import SizeInput from 'components/UI/SizeInput';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import routes from 'helpers/routes';
import { useTranslation } from 'next-i18next';
import Image from 'next/image';
import Link from 'next/link';
import React from 'react';
import { useSelector } from 'react-redux';
import { getCheckoutData } from 'store/checkout/CheckoutStore';
import { getCheckoutProduct } from 'store/product/CheckoutProductStore';
import fav from 'public/static/img/fav.svg';
import favActive from 'public/static/img/favActive.svg';

import styles from './ProductList.module.scss';
import FavoritesApi from 'api/favorites/FavoritesApi';
import { getFavoritesIds } from 'store/favorites/FavoritesStore';
import { useRouter } from 'next/router';
import ym from 'react-yandex-metrika';
import { orderArrModel } from 'api/checkout/Models/checkoutModel';

const favoritesStore = new FavoritesApi();

type ProductListTypes = {
	products: Pick<orderArrModel, 'products'>['products'];
};

const ProductList = ({ products }: ProductListTypes) => {
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);
	const deliveryId = useSelector((state: any) => state.checkout.deliveryId);
	const paymentId = useSelector((state: any) => state.checkout.paymentId);
	const checkoutIsLoaded = useSelector((state: any) => state.checkout.checkoutIsLoaded);
	const promo = useSelector((state: any) => state.checkout.promo);
	const favoritesIds = useSelector((state: any) => state.favorites.favoritesIds);
	const token = useSelector((state: any) => state.userData.token);

	const dispatch = useAppDispatch();

	const router = useRouter();

	const { t } = useTranslation();

	const onClickSizeHandler = (event: React.MouseEvent<HTMLDivElement>, id: number) => {
		const currentId = products.find(item => item.id === id);
		const selectedSize = currentId.sizeList.find(item => item.name === event.currentTarget.textContent).id;
		if (currentId.id === selectedSize) return;
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
				basket: {
					update: {
						[currentId.id]: {
							replace: selectedSize,
						},
					},
				},
			})
		);
	};

	const leftBtnClick = (id: number, count: number) => {
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
				basket: {
					update: {
						[id]: {
							quantity: --count,
						},
					},
				},
			})
		);
	};

	const rightBtnClick = (id: number, count: number) => {
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
				basket: {
					update: {
						[id]: {
							quantity: ++count,
						},
					},
				},
			})
		);
	};

	const deleteProductHandler = (id: number) => {
		dispatch(
			getCheckoutData({
				country: currentCountry,
				coupon: promo,
				location: currentCity,
				personTypeId: 1,
				deliveryId,
				paymentId,
				basket: {
					delete: id,
				},
			})
		).then(() => {
			process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'delete-from-basket');
			process.env.NEXT_PUBLIC_IS_PRODUCTION === 'true' &&
				(window as any).dataLayer.push({
					ecommerce: {
						currencyCode: 'RUB',
						remove: {
							products: products
								.map(item => {
									return {
										id: item.id,
										name: item.name,
										category: item.category,
										price: parseInt(item.price.replace(' ', '')),
										quantity: item.count,
										variant: item.color,
									};
								})
								.find(item => item.id === id),
						},
					},
				});
			dispatch(getCheckoutProduct({ token: localStorage.getItem('token') || null }));
		});
	};

	const addToFavorites = (id: number) => {
		favoritesStore.addToFavorites({ id }).then(res => {
			if (res.data.message === 'ok') {
				process.env.NEXT_PUBLIC_YANDEX_METRIKA_ID && ym('reachGoal', 'add-to-favorites');
				dispatch(getFavoritesIds({ token: localStorage.getItem('token') || null }));
			}
		});
	};

	const deleteToFavorites = (id: number) => {
		favoritesStore.deleteFavorites({ id }).then(res => {
			if (res.data.message === 'ok') {
				dispatch(getFavoritesIds({ token: localStorage.getItem('token') || null }));
			}
		});
	};

	return (
		<div
			style={products.length >= 3 && !isMobile ? { overflowY: 'scroll' } : { overflowY: 'hidden' }}
			className={styles.products}
		>
			{products.map((item, index) => {
				return (
					<div style={{ zIndex: (products.length - index).toString() }} className={styles.productItem} key={item.id}>
						<div className={styles.productInfo}>
							<Image
								src={item.img}
								onLoadingComplete={img => (img.style.backgroundImage = 'none')}
								alt={'img-' + item.id}
								width={140}
								height={174}
							/>
							<div>
								{!isMobile ? (
									<>
										<div>
											<Link target="_blank" href={routes.productCard + item.id}>
												<p>{item.name}</p>
											</Link>
											<p>: {item.color}</p>
											{item.storeCount === 0 && <p className={styles.outOfStock}>  </p>}
										</div>
										<p>{item.price}</p>
									</>
								) : (
									<>
										<div>
											<Link target="_blank" href={routes.productCard + item.id}>
												<p>{item.name}</p>
											</Link>
											<p>{item.price}</p>
										</div>
										<p>: {item.color}</p>
										{item.storeCount === 0 && <p className={styles.outOfStock}>  </p>}
									</>
								)}
							</div>
						</div>
						<div className={styles.counterAndSizeBlock}>
							{!isMobile && Array.isArray(item.sizeList) && item.sizeList.length !== 0 && (
								<SizeInput
									sizeList={item.sizeList}
									disable={!checkoutIsLoaded || item.storeCount === 0}
									currentSize={item.currentSize}
									onClickSizeHandler={event => onClickSizeHandler(event, item.id)}
								/>
							)}
							{isMobile && Array.isArray(item.sizeList) && item.sizeList.length !== 0 && (
								<div className={styles.sizeList}>
									<p>{t('size')}</p>
									<Dropdown
										isOnlySelect
										value={item.sizeList.find(findItem => findItem.name === item.currentSize)?.name}
										disable={!checkoutIsLoaded || item.storeCount === 0}
										dropdata={item.sizeList}
										withSuisse
										clickOnDropData={event => onClickSizeHandler(event, item.id)}
									/>
								</div>
							)}
							<CounterInput
								count={item.count}
								disable={!checkoutIsLoaded || item.storeCount === 0}
								leftBtnClick={() => leftBtnClick(item.id, item.count)}
								rightBtnClick={() => rightBtnClick(item.id, item.count)}
							/>
						</div>
						<div className={styles.buttonGroup}>
							<Image
								src={favoritesIds.some(someItem => someItem === item.id) ? favActive : fav}
								width={isMobile ? 16 : 24}
								height={isMobile ? 16 : 24}
								alt="fav"
								onClick={
									token
										? !favoritesIds.some(someItem => someItem === item.id)
											? () => addToFavorites(item.id)
											: () => deleteToFavorites(item.id)
										: () => router.push(router.asPath + routes.signIn)
								}
							/>
							<Image
								onClick={checkoutIsLoaded ? () => deleteProductHandler(item.id) : null}
								src="/static/img/closeDark.svg"
								width={isMobile ? 16 : 24}
								height={isMobile ? 16 : 24}
								alt="X"
							/>
						</div>
					</div>
				);
			})}
		</div>
	);
};

export default ProductList;
