import Dropdown from 'components/UI/Dropdown';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import useDebounce from 'helpers/hooks/useDebounce';
import React from 'react';
import { useSelector } from 'react-redux';
import { getCheckoutData } from 'store/checkout/CheckoutStore';
import {
	changeCities,
	changeCountryies,
	changeCurrentCity,
	changeCurrentCountry,
	getCityList,
	getCountryList,
} from 'store/checkout/LocationDataStore';

import styles from './LocationBlock.module.scss';

const LocationBlock = () => {
	const currentCountry = useSelector((state: any) => state.locationData.currentCountry);
	const countryies = useSelector((state: any) => state.locationData.countryies);
	const currentCity = useSelector((state: any) => state.locationData.currentCity);
	const cities = useSelector((state: any) => state.locationData.cities);
	const deliveryId = useSelector((state: any) => state.checkout.deliveryId);
	const paymentId = useSelector((state: any) => state.checkout.paymentId);
	const promo = useSelector((state: any) => state.checkout.promo);

	const [country, setCountry] = React.useState<string>(currentCountry ? currentCountry : '');
	const [city, setCity] = React.useState<string>(currentCity);
	const [countryIsReadonly, setCountryIsReadonly] = React.useState<boolean>(true);
	const [cityIsReadonly, setCityIsReadonly] = React.useState<boolean>(true);
	const [offsetCity, setOffsetCity] = React.useState<number>(0);
	const [countCity, setCountCity] = React.useState<number>(0);

	const debounceCountryies = useDebounce(country, 300);
	const debounceCities = useDebounce(city, 300);

	const dispatch = useAppDispatch();

	const changeCountry = (event: React.ChangeEvent<HTMLInputElement>) => {
		setCountry(event.target.value);
		setCountryIsReadonly(false);
		setCity('');
		setCityIsReadonly(false);
		setOffsetCity(0);
		setCountCity(0);
		dispatch(changeCurrentCity({ currentCity: '', currentCityCode: '' }));
	};

	const clickOnDropDataCountry = (event: React.MouseEvent<HTMLDivElement>) => {
		if (event.currentTarget.textContent === '  ') {
			setCountry(country);
			dispatch(changeCurrentCountry(country));
		} else {
			setCountry(countryies.find(item => item.name === event.currentTarget.textContent).name);
			dispatch(changeCurrentCountry(countryies.find(item => item.name === event.currentTarget.textContent).name));
		}
		dispatch(changeCountryies([]));
		setCountryIsReadonly(true);
	};

	const changeCity = (event: React.ChangeEvent<HTMLInputElement>) => {
		setCity(event.target.value);
		setCityIsReadonly(false);
		setOffsetCity(0);
		setCountCity(0);
	};

	const clickOnDropDataCity = (event: React.MouseEvent<HTMLDivElement>) => {
		const selectedCity = cities.find(item => item.name === event.currentTarget.textContent);
		setCity(selectedCity.name === '  ' ? city : selectedCity.name);
		dispatch(
			changeCurrentCity({
				currentCity: selectedCity.name === '  ' ? city : selectedCity.name,
				currentCityCode: selectedCity.name === '  ' ? '' : selectedCity.code,
			})
		);
		dispatch(changeCities([]));
		setCityIsReadonly(true);
		dispatch(
			getCheckoutData({
				country: country === currentCountry ? currentCountry : '',
				coupon: promo,
				location: selectedCity.name === '  ' ? selectedCity.id : selectedCity.name,
				personTypeId: 1,
				deliveryId,
				paymentId,
			})
		);
	};

	const loadMoreCity = () => {
		dispatch(
			getCityList({
				countryId: Array.isArray(countryies) ? countryies.find(item => item.name === debounceCountryies)?.id : '',
				query: debounceCities,
				limit: 6,
				offset: offsetCity,
			})
		).then(res => {
			const response = res.payload as any;
			setCountCity(response.data.meta.count);
			setOffsetCity(offsetCity + response.data.meta.offset);
		});
	};

	React.useEffect(() => {
		dispatch(getCountryList({ query: debounceCountryies }));
	}, [debounceCountryies]);

	React.useEffect(() => {
		dispatch(
			getCityList({
				countryId: Array.isArray(countryies) ? countryies.find(item => item.name === debounceCountryies)?.id : '',
				query: debounceCities,
				limit: 6,
				offset: offsetCity,
			})
		).then(res => {
			const response = res.payload as any;
			if (response.data.data.length === 1) {
				dispatch(
					getCheckoutData({
						country: currentCountry,
						coupon: promo,
						location: response.data.data[0].name,
						personTypeId: 1,
						deliveryId,
						paymentId,
					})
				);
			}
			setCountCity(response.data.meta.count);
			setOffsetCity(offsetCity + response.data.meta.offset);
		});
	}, [debounceCities]);

	return (
		<div className={styles.location}>
			<Dropdown
				placeholder=""
				value={country}
				readonly={countryIsReadonly}
				dropdata={countryies}
				withSuisse
				isError={country.length === 0}
				onChange={changeCountry}
				clickOnDropData={clickOnDropDataCountry}
			/>
			<Dropdown
				inputType="text"
				placeholder=""
				value={city}
				readonly={cityIsReadonly}
				disable={country.length === 0}
				dropdata={cities}
				infinityScroll
				hasMore={Array.isArray(cities) ? countCity > cities.length : false}
				onChange={changeCity}
				clickOnDropData={clickOnDropDataCity}
				fetchData={loadMoreCity}
			/>
		</div>
	);
};

export default React.memo(LocationBlock);
