import Button from 'components/UI/Button';
import routes from 'helpers/routes';
import { SuisseIntl } from 'helpers/utils/fonts';
import Link from 'next/link';
import { useRouter } from 'next/router';
import React from 'react';

import styles from './OrderInfo.module.scss';

type orderType = 'create' | 'paid' | 'fail' | 'empty';

type actionType = '' | '';

type OrderInfoTypes = {
	orderId?: string;
	type: orderType;
	action?: actionType;
	paymentBtn?: string;
};

const OrderInfo = ({ orderId, type, action, paymentBtn }: OrderInfoTypes) => {
	const router = useRouter();

	const divRef = React.useRef(null);

	React.useEffect(() => {
		const slotHtml = document
			.createRange()
			.createContextualFragment(paymentBtn && paymentBtn.replace('$("#payButton").on("click", payHandler);', '')); // Create a 'tiny' document and parse the html string
		divRef.current && (divRef.current.innerHTML = ''); // Clear the container
		divRef.current && divRef.current.appendChild(slotHtml); // Append the new content
		divRef.current &&
			divRef.current.querySelector('script').addEventListener('load', () => {
				setTimeout(() => {
					try {
						//@ts-ignore
						document.querySelector<HTMLButtonElement>('#payButton').onclick = payHandler;
						divRef.current.querySelector('#payButton').click();
					} catch (e) {
						document.querySelector<HTMLButtonElement>('#payButton').style.display = 'none';
					}
				}, 500);
			});
	}, [paymentBtn, divRef.current]);

	if (type === 'create' || type === 'paid') {
		return (
			<div className={styles.orderInfo}>
				<p className={SuisseIntl.className}>
					  <b>{orderId}</b> {action}.
				</p>
				<p>
					 -      .     
					<Link href={routes.checkout + routes.lk}> </Link>.
				</p>
				{paymentBtn && <div ref={divRef}></div>}
			</div>
		);
	} else if (type === 'fail') {
		return (
			<div className={styles.orderInfo}>
				<p className={SuisseIntl.className}> {orderId}  .</p>
				<p>    -       .</p>
			</div>
		);
	} else if (type === 'empty') {
		return (
			<div className={styles.orderInfo}>
				<p className={SuisseIntl.className}>   ..</p>
				<Button onClick={() => router.push(routes.home)} text="  " isInvisible />
			</div>
		);
	}
};

export default OrderInfo;
