import Col from 'components/UI/Col';
import Input from 'components/UI/Input';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import React from 'react';
import { useSelector } from 'react-redux';
import MaskedInput from 'react-text-mask';
import { changePersonalData } from 'store/checkout/PersonalDataStore';

import styles from './PersonalDataForm.module.scss';

type fieldsTypes = {
	value: string;
	placeholder: string;
	type: string;
	disable: boolean;
	code: string;
	isError: boolean;
	errorText: string;
	id: string;
}[];

const phoneFormatter = (phone: any, phoneCheckout: any) => {
	return phoneCheckout.value
		? phoneCheckout.value[0] === '+'
			? phoneCheckout.value[0] + phoneCheckout.value.slice(1, phoneCheckout.value.length).replace(/[^\d]/g, '')
			: phoneCheckout.value.replace(/[^\d]/g, '')
		: phone
		? phone[0] === '+'
			? phone[0] + phone.slice(1, phone.length).replace(/[^\d]/g, '')
			: phone.replace(/[^\d]/g, '')
		: '';
};

const PersonalDataForm = () => {
	const firstName = useSelector((state: any) => state.userData.firstName);
	const lastName = useSelector((state: any) => state.userData.lastName);
	const email = useSelector((state: any) => state.userData.email);
	const phone = useSelector((state: any) => state.userData.phone);
	const firstNameCheckout = useSelector((state: any) => state.personalData.name);
	const lastNameCheckout = useSelector((state: any) => state.personalData.lastName);
	const surname = useSelector((state: any) => state.personalData.surname);
	const emailCheckout = useSelector((state: any) => state.personalData.email);
	const phoneCheckout = useSelector((state: any) => state.personalData.phone);
	const isMobile = useSelector((state: any) => state.isMobile.isMobile);

	const [fieldList, setFieldList] = React.useState<fieldsTypes>([]);
	const [location, setLocation] = React.useState<string>('');

	const dispatch = useAppDispatch();

	React.useEffect(() => {
		dispatch(
			changePersonalData({
				email: email || emailCheckout.value,
				phone: phone
					? phone[0] === '+'
						? phone[0] + phone.slice(1, phone.length).replace(/[^\d]/g, '')
						: phone.replace(/[^\d]/g, '')
					: phoneCheckout.value
					? phoneCheckout.value[0] === '+'
						? phoneCheckout.value[0] + phoneCheckout.value.slice(1, phoneCheckout.value.length).replace(/[^\d]/g, '')
						: phoneCheckout.value.replace(/[^\d]/g, '')
					: '',
				surname: surname.value,
				name: firstName || firstNameCheckout.value,
				lastName: lastName || lastNameCheckout.value,
			})
		);
	}, [email, phone, firstName, lastName]);

	React.useEffect(() => {
		setLocation(localStorage.getItem('location'));
	}, []);

	React.useEffect(() => {
		const fields = [
			{
				value: email || '',
				placeholder: 'Email',
				type: 'email',
				disable: !!email,
				code: emailCheckout.code,
				isError: emailCheckout.isError,
				errorText: emailCheckout.errorText,
				id: 'email',
			},
			{
				value: phone || '',
				placeholder: '+7 999 000 00 00',
				type: 'phone',
				disable: !!phone,
				code: phoneCheckout.code,
				isError: phoneCheckout.isError,
				errorText: phoneCheckout.errorText,
				id: 'phone',
			},
			{
				value: lastName || '',
				placeholder: '',
				type: 'text',
				disable: !!lastName,
				code: lastNameCheckout.code,
				isError: lastNameCheckout.isError,
				errorText: lastNameCheckout.errorText,
				id: 'lastName',
			},
			{
				value: firstName || '',
				placeholder: '',
				type: 'text',
				disable: !!firstName,
				code: firstNameCheckout.code,
				isError: firstNameCheckout.isError,
				errorText: firstNameCheckout.errorText,
				id: 'name',
			},
			{
				value: '',
				placeholder: '',
				type: 'text',
				disable: false,
				code: surname.code,
				isError: surname.isError,
				errorText: surname.errorText,
				id: 'surname',
			},
		];
		setFieldList([...fields]);
	}, [email, phone, lastName, firstName]);

	const onChangeInputs = (event: React.ChangeEvent<HTMLInputElement>, id: string) => {
		setFieldList(prev => {
			return prev.map(item => {
				if (item.id === id) {
					return { ...item, value: event.target.value };
				}
				return item;
			});
		});
		dispatch(
			changePersonalData({
				email: id === 'email' ? event.target.value : emailCheckout.value,
				phone:
					id === 'phone'
						? event.target.value[0] === '+'
							? event.target.value[0] + event.target.value.slice(1, event.target.value.length).replace(/[^\d]/g, '')
							: event.target.value.replace(/[^\d]/g, '')
						: phoneCheckout.value[0] === '+'
						? phoneCheckout.value[0] + phoneCheckout.value.slice(1, phoneCheckout.value.length).replace(/[^\d]/g, '')
						: phoneCheckout.value.replace(/[^\d]/g, ''),
				surname: id === 'surname' ? event.target.value : surname.value,
				name: id === 'name' ? event.target.value : firstNameCheckout.value,
				lastName: id === 'lastName' ? event.target.value : lastNameCheckout.value,
			})
		);
	};

	return (
		<Col className={styles.personalData} marginTop={32} marginBottom={isMobile ? 18 : 60}>
			<>
				{fieldList.map(item => {
					return (
						<React.Fragment key={item.id}>
							{item.type === 'phone' && location === 'RU' ? (
								<div>
									<MaskedInput
										className={`${styles.inputMask} ${item.isError ? styles.inputError : ''}`}
										mask={[
											'+',
											'7',
											' ',
											/[1-6]|[8-9]/,
											/\d/,
											/\d/,
											' ',
											/\d/,
											/\d/,
											/\d/,
											' ',
											/\d/,
											/\d/,
											' ',
											/\d/,
											/\d/,
										]}
										guide
										id={item.code}
										type="tel"
										placeholder={item.placeholder}
										onChange={event => onChangeInputs(event, item.id)}
										value={item.value}
									/>
									{item.isError && <p className={styles.errorText}>{item.errorText}</p>}
								</div>
							) : (
								<Input
									placeholder={item.placeholder}
									value={item.value}
									type={item.type}
									id={item.code}
									isError={
										item.id === 'email'
											? emailCheckout.isError
											: item.id === 'phone'
											? phoneCheckout.isError
											: item.id === 'lastName'
											? lastNameCheckout.isError
											: item.id === 'name'
											? firstNameCheckout.isError
											: surname.isError
									}
									errorText={
										item.id === 'email'
											? emailCheckout.errorText
											: item.id === 'phone'
											? phoneCheckout.errorText
											: item.id === 'lastName'
											? lastNameCheckout.errorText
											: item.id === 'name'
											? firstNameCheckout.errorText
											: surname.errorText
									}
									readonly={item.disable}
									onChange={event => onChangeInputs(event, item.id)}
								/>
							)}
						</React.Fragment>
					);
				})}

				<p>,   </p>
			</>
		</Col>
	);
};

export default React.memo(PersonalDataForm);
